import * as React from 'react';
import { styled, createTheme, ThemeProvider } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import MuiDrawer from '@mui/material/Drawer';
import Box from '@mui/material/Box';
import MuiAppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import List from '@mui/material/List';
import Typography from '@mui/material/Typography';
import Divider from '@mui/material/Divider';
import IconButton from '@mui/material/IconButton';
import Badge from '@mui/material/Badge';
import Container from '@mui/material/Container';
import Grid from '@mui/material/Grid';
import Link from '@mui/material/Link';
import MenuIcon from '@mui/icons-material/Menu';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import NotificationsIcon from '@mui/icons-material/Notifications';
import { mainListItems, secondaryListItems } from '../listItems';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';

function Copyright(props) {
  return (
    <Typography variant="body2" color="text.secondary" align="center" {...props}>
      {'Copyright © '}
      <Link color="inherit" href="https://mui.com/">
        {/* Your Website */}
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

const drawerWidth = 240;

const AppBar = styled(MuiAppBar, {
  shouldForwardProp: (prop) => prop !== 'open',
})(({ theme, open }) => ({
  zIndex: theme.zIndex.drawer + 1,
  transition: theme.transitions.create(['width', 'margin'], {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  ...(open && {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  }),
}));

const Drawer = styled(MuiDrawer, { shouldForwardProp: (prop) => prop !== 'open' })(
  ({ theme, open }) => ({
    '& .MuiDrawer-paper': {
      position: 'relative',
      whiteSpace: 'nowrap',
      width: drawerWidth,
      transition: theme.transitions.create('width', {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.enteringScreen,
      }),
      boxSizing: 'border-box',
      ...(!open && {
        overflowX: 'hidden',
        transition: theme.transitions.create('width', {
          easing: theme.transitions.easing.sharp,
          duration: theme.transitions.duration.leavingScreen,
        }),
        width: theme.spacing(7),
        [theme.breakpoints.up('sm')]: {
          width: theme.spacing(9),
        },
      }),
    },
  }),
);

// TODO remove, this demo shouldn't need to reset the theme.
const defaultTheme = createTheme();

export default function TellerPlaceBet() {
  const [open, setOpen] = React.useState(true);
  const toggleDrawer = () => {
    setOpen(!open);
  };

  return (
    <ThemeProvider theme={defaultTheme}>
      <Box sx={{ display: 'flex' }}>
        <CssBaseline />
        <AppBar position="absolute" open={open}>
          <Toolbar
            sx={{
              pr: '24px', // keep right padding when drawer closed
            }}
          >
            <IconButton
              edge="start"
              color="inherit"
              aria-label="open drawer"
              onClick={toggleDrawer}
              sx={{
                marginRight: '36px',
                ...(open && { display: 'none' }),
              }}
            >
              <MenuIcon />
            </IconButton>
            <Typography
              component="h1"
              variant="h6"
              color="inherit"
              noWrap
              sx={{ flexGrow: 1 }}
            >
              Bets
            </Typography>
            <IconButton color="inherit">
              <Badge badgeContent={4} color="secondary">
                <NotificationsIcon />
              </Badge>
            </IconButton>
          </Toolbar>
        </AppBar>
        <Drawer variant="permanent" open={open}>
          <Toolbar
            sx={{
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'flex-end',
              px: [1],
            }}
          >
            
            <b>GMA COLLISEUM &nbsp;&nbsp;&nbsp; </b>
            <IconButton onClick={toggleDrawer}>
              <ChevronLeftIcon />
            </IconButton>
          </Toolbar>
          <Divider />
          <List component="nav">
            {mainListItems}
            <Divider sx={{ my: 1 }} />
            {secondaryListItems}
          </List>
        </Drawer>
        <Box
          component="main"
          sx={{
            backgroundColor: (theme) =>
              theme.palette.mode === 'light'
                ? theme.palette.grey[100]
                : theme.palette.grey[900],
            flexGrow: 1,
            height: '100vh',
            overflow: 'auto',
          }}
        >
          <Toolbar />
          <Container maxWidth="false" sx={{ mt: 4, mb: 4 }}>
            <h3>Credits: PHP 100,000.00</h3>
            <h3>FIGHT - 116</h3>

            <Grid container  spacing="2">
              <Grid item md={4} xs={12}>
                <Card style={{textAlign:"center",backgroundColor:"red"}}>
                  <CardContent>
                    <Typography sx={{ fontSize: 32,color:"white" }} color="text.secondary" gutterBottom>
                      MERON
                    </Typography>
                    <Typography variant="h5" component="div">
                    </Typography>
                    <Typography sx={{ mb: 1.5, color:"white" }} color="text.secondary">
                      PAYOUT = 1.67
                    </Typography>
                    <Typography variant="body2" sx={{color:"white"}}>
                     0/100000
                    </Typography>
                  </CardContent>
                </Card>
              </Grid>

              <Grid item md={4} xs={12}>
                <Card style={{textAlign:"center",backgroundColor:"green"}}>
                  <CardContent>
                    <Typography sx={{ fontSize: 32,color:"white" }} color="text.secondary" gutterBottom>
                      DRAW
                    </Typography>
                    <Typography variant="h5" component="div">
                    </Typography>
                    <Typography sx={{ mb: 1.5, color:"white" }} color="text.secondary">
                      PAYOUT = 1.67
                    </Typography>
                    <Typography variant="body2" sx={{color:"white"}}>
                     0/100000
                    </Typography>
                  </CardContent>
                </Card>
              </Grid>

              <Grid item md={4} xs={12}>
                <Card style={{textAlign:"center",backgroundColor:"blue"}}>
                  <CardContent>
                    <Typography sx={{ fontSize: 32,color:"white" }} color="text.secondary" gutterBottom>
                      WALA
                    </Typography>
                    <Typography variant="h5" component="div">
                    </Typography>
                    <Typography sx={{ mb: 1.5, color:"white" }} color="text.secondary">
                      PAYOUT = 1.67
                    </Typography>
                    <Typography variant="body2" sx={{color:"white"}}>
                     0/100000
                    </Typography>
                  </CardContent>
                </Card>
              </Grid>
            </Grid>

            <br />
            <Grid container  spacing="2">
              <Grid item md={12} xs={12}>
                <Card>
                    <CardContent>
                    <TextField id="outlined-basic" label="Bet" variant="outlined" sx={{minWidth:"400px"}} /> &nbsp;
                    <Button variant="outlined" size="large" sx={{mt:0.5}}>Place Bet</Button>
                    <br />
                    <br />
                      <Button variant="contained">100</Button>&nbsp;&nbsp;&nbsp;&nbsp;
                      <Button variant="contained">200</Button>&nbsp;&nbsp;&nbsp;&nbsp;
                      <Button variant="contained">500</Button>&nbsp;&nbsp;&nbsp;&nbsp;
                      <Button variant="contained">1000</Button>&nbsp;&nbsp;&nbsp;&nbsp;
                      <Button variant="contained">5000</Button>&nbsp;&nbsp;&nbsp;&nbsp;
                      
                    </CardContent>
                  </Card>
                </Grid>
            </Grid>

          

            <Copyright sx={{ pt: 4 }} />
          </Container>
        </Box>
      </Box>
    </ThemeProvider>
  );
}