
import * as React from 'react';
import { styled } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import { Card, CardMedia } from '@mui/material';

const Item = styled(Paper)(({ theme }) => ({
  backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: 'center',
  color: theme.palette.text.secondary,
  borderRadius:"0"
}));

const PublicIndexPage = () => {

    
    return (
    <>
        <Box sx={{ flexGrow: 1 }}>
        <Grid container spacing="2">
            <Grid item md={5} xs={12}  style={{borderColor:"yellow",borderWidth:"5px"}}>
                <Item style={{backgroundColor:"red",color:"white"}}><h4>MERON</h4></Item>

                <Grid container>
                    <Grid item md={6} xs={12}>
                    <Item style={{backgroundColor:"black"}}>
                        <h5 style={{color:"yellow"}}>ALL BETS</h5>
                        <p style={{color:"white"}}>2,214,231</p>
                    </Item>
                    </Grid>
                    <Grid item md={6} xs={12}>
                    <Item style={{backgroundColor:"black",color:"yellow"}}>
                        <h5 style={{color:"yellow"}}>PAYOUT EVERY 100</h5>
                        <p style={{color:"white"}}>173.64</p>
                    </Item>
                    </Grid>
                </Grid>
            </Grid>
            <Grid item md={2} xs={12}>
                <Item style={{backgroundColor:"green",color:"white"}}><h4>DRAW</h4></Item>
            
                
                <Grid item md={12} xs={12}>
                    <Item style={{backgroundColor:"black"}}>
                        <h5 style={{color:"yellow"}}>1-6</h5>
                        <p style={{color:"white"}}>2,214,231</p>
                    </Item>
                </Grid>

            </Grid>

            <Grid item md={5} xs={12}>
                <Item style={{backgroundColor:"blue",color:"white"}}><h4>WALA</h4></Item>

                <Grid container>
                    <Grid item md={6} xs={12}>
                    <Item style={{backgroundColor:"black"}}>
                        <h5 style={{color:"yellow"}}>ALL BETS</h5>
                        <p style={{color:"white"}}>2,214,231</p>
                    </Item>
                    </Grid>
                    <Grid item md={6} xs={12}>
                    <Item style={{backgroundColor:"black",color:"yellow"}}>
                        <h5 style={{color:"yellow"}}>PAYOUT EVERY 100</h5>
                        <p style={{color:"white"}}>173.64</p>
                    </Item>
                    </Grid>
                </Grid>
            </Grid>

            <Grid item md={12} xs={12}>
                <Item>
                <div>
                    <iframe
                        width="100%"
                        height="750px"
                        src="https://www.youtube.com/embed/nWwpyclIEu4"
                        title="YouTube Video"
                        allow=''
                        allowFullScreen
                    ></iframe>
                </div>

                </Item>
            </Grid>
        </Grid>
        </Box>
    </>
    )


}

export default PublicIndexPage;